.container-EcoSnapCard {
    height: 100%;
    width: 100%;
    background-color: #004c52;
    border-radius: 15px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.card-EcoSnapCard {
    height: calc(100% - 10px);
    width: calc(100% - 10px);
    border-radius: 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    background-image: url("../../assets/Projects/EcoSnap/CardCover.png");
    background-position: center;
    background-size: cover;
}

.logo-EcoSnapCard {
    width: 50%;
}

.onHoverContainer-EcoSnapCard {
    height: 100%;
    width: 100%;
    border-radius: 10px;
    background: rgba(0, 0, 0, 0.7);
    color: #FFFFFF;
    padding: 20px 30px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: start;
        align-items: flex-start;
    opacity: 1;
    -webkit-animation-name: fadeInOpacity;
            animation-name: fadeInOpacity;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    -webkit-animation-duration: 0.8s;
            animation-duration: 0.8s;
}

.backLogo-EcoSnapCard {
    width: 45%;
    height: auto;
}

.PositionTimelineContainer-EcoSnapCard {
    width: 100%;
    padding: 25px 0 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.Position-EcoSnapCard {
    font-size: 22px;
    font-weight: 600;
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    text-align: left;
}

.Timeline-EcoSnapCard {
    font-size: 16px;
    font-weight: 600;
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    text-align: end;
}

/*Animations*/
@-webkit-keyframes fadeInOpacity {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
@keyframes fadeInOpacity {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}


