.container-PeekaVRCard {
    height: 100%;
    width: 100%;
    background-image:
        -webkit-gradient(
            linear,
            left top, left bottom,
            from(#ff339a),
            to(#ff8756)
        );
    background-image:
        -webkit-linear-gradient(
            #ff339a,
            #ff8756
        );
    background-image:
        -o-linear-gradient(
            #ff339a,
            #ff8756
        );
    background-image:
        linear-gradient(
            #ff339a,
            #ff8756
        );
    border-radius: 15px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.card-PeekaVRCard {
    height: calc(100% - 10px);
    width: calc(100% - 10px);
    border-radius: 10px;
    background-image: url("./../../assets/Projects/PeekaVR/white_background.png");
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.logo-PeekaVRCard {

}

.onHoverContainer-PeekaVRCard {
    height: 100%;
    width: 100%;
    border-radius: 10px;
    background: rgba(0, 0, 0, 0.7);
    color: #FFFFFF;
    padding: 20px 30px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    opacity: 1;
    -webkit-animation-name: fadeInOpacity;
            animation-name: fadeInOpacity;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    -webkit-animation-duration: 0.8s;
            animation-duration: 0.8s;
}

.onHoverLogo-PeekaVRCard {
    width: 35%;
    height: auto;
}

.PositionTimelineContainer-PeekaVRCard {
    width: 100%;
    padding: 25px 0 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.Position-PeekaVRCard {
    font-size: 22px;
    font-weight: 600;
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    text-align: left;
}

.Timeline-PeekaVRCard {
    font-size: 16px;
    font-weight: 600;
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    text-align: end;
}

/*Animations*/
@-webkit-keyframes fadeInOpacity {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
@keyframes fadeInOpacity {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}


